import { ref } from 'vue';
import { fromCode, getdate } from '@/utils/validate';
import jjdx from "@/assets/jjdx.png";
export default {
  setup() {
    const XnxqOption = ref("");
    const KcOption = ref("");
    const BjOption = ref("");
    const values = ref([]);
    const TestOption = [{
      value: '0',
      label: 'Form',
      children: [{
        value: '0',
        label: 'Radio'
      }, {
        value: '0',
        label: 'Checkbox'
      }]
    }, {
      value: 'test',
      label: '测试',
      children: [{
        value: 'radio',
        label: 'Radio'
      }, {
        value: 'checkbox',
        label: 'Checkbox'
      }]
    }];
    const dialogVisible = ref(false);
    const manageVisible = ref(false);
    const loading = ref(false);

    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    return {
      XnxqOption,
      dialogVisible,
      manageVisible,
      handleClose,
      getdate,
      KcOption,
      BjOption,
      loading,
      TestOption,
      values
    };
  },

  data() {
    return {
      // 分页
      currentPage: 1,
      pagesize: 10,
      sections: ["周次\\节", "1-2节", "3-4节", "5-6节", "7-8节", "9-10节"],
      userInfo: null,
      XnxqList: [],
      Xnxq: {},
      search: "",
      // 选课的结果
      XkList: [],
      xnxqh: "",
      //
      logo: jjdx,
      // 课程和 班级 列表
      KcAndBjList: [],
      // 班级列表
      Bjlist: [],
      managePaike: {} // 测试级 联动

    };
  },

  created() {
    const user = sessionStorage.getItem("userInfo");

    if (user) {
      this.userInfo = JSON.parse(fromCode(user)); // 根据请求获取用户信息
    } else {
      this.$router.push("/login");
    }

    this.getXnxqList();
  },

  methods: {
    // 分页
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    handleChange(value) {
      console.log(JSON.stringify(value));
      console.log(value);
      this.processSysInfo();
    },

    // 获取 学期学年列表
    getXnxqList() {
      this.$axios.get("/weixin-ggjxzl/getXnxqList").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.XnxqList = data.data;
        } else {
          ElMessage.error("获取学年数据错误");
        }
      });
    },

    // 根据 学期， 教师id 获取 实验室课表
    getXkListByXnxqAndJgId(xnxq, JgId) {
      this.$axios.post("/weixin-sysxk/Jgpklist", {
        "Xnxq": xnxq,
        "JgId": JgId
      }).then(res => {
        const data = res.data;
        this.xnxqh = xnxq;

        if (data.code == 200) {
          this.XkList = data.data;

          if (!this.XkList.length) {
            ElMessage.success("查询无数据");
          }
        }
      });
    },

    // 选择学年
    changeXnxq() {
      this.Xnxq = JSON.parse(JSON.stringify(this.XnxqOption));
      this.XnxqOption = this.Xnxq.xnxqh;
      Loading.show();
      this.getKcAndBjList(this.Xnxq.xnxqh, this.userInfo.useraccount);
      Loading.hide();
    },

    // 根据学期号 和 教师号 获取课程 以及班级
    getKcAndBjList(Xnxqh, Jgh) {
      this.$axios.post("/weixin-jskb/getjskb", {
        "Xnxqh": Xnxqh,
        "Jgh": Jgh
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.KcAndBjList = data.data;
        }
      });
    },

    // 选择课程
    changeKc() {
      const arr = this.KcAndBjList.filter(i => {
        return i.kcmc == this.KcOption;
      });

      if (arr.length == 1) {
        this.Bjlist = arr[0].classList;
      }
    },

    // 选择班级
    changeBj(item) {
      console.log(this.BjOption);
      console.log(item);
    },

    QuerySchedule() {
      // 判断 是否 选择了学期
      if (this.XnxqOption) {
        if (this.xnxqh != this.Xnxq.xnxqh) {
          Loading.show();
          this.getXkListByXnxqAndJgId(this.Xnxq.xnxqh, this.userInfo.useraccount);
          Loading.hide();
        }
      } else {
        ElMessage.error("请选择查询的学期");
      }
    },

    ExportSchedule() {
      // 判断 是否有数据
      if (this.XnxqOption) {
        if (this.XkList.length) {
          this.dialogVisible = true;
        } else {
          ElMessage.error("无数据导出，点击查询试试");
        }
      } else {
        ElMessage.error("请先选择学期，点击查询");
      }
    },

    ExportPdf() {
      this.$pdf.getPdf('pdfDom', "实验室课程表" + getdate().replace(/\s/g, ""));
    },

    changePaikeInfo(item) {
      // 查询自己 当前学期的 课程
      this.managePaike = JSON.parse(JSON.stringify(item));
      this.manageVisible = true;
      console.log(this.managePaike);
    },

    deletePaikeById(item) {
      // 校验参数
      if (this.XnxqOption) {
        this.$axios.post("/weixin-sysxk/cancleXk", {
          "xnxq01ID": this.Xnxq.xnxqh,
          "kkzc": item.kkzc,
          "kksjmx": item.kkDay + "-" + item.kkSection,
          "jg0101ID": this.userInfo.useraccount,
          "sysh": item.sysh
        }).then(res => {
          const data = res.data;

          if (data.code == 200) {
            ElMessage.success("删除成功!");
            Loading.show();
            this.getXkListByXnxqAndJgId(this.Xnxq.xnxqh, this.userInfo.useraccount);
            Loading.hide();
          } else {
            ElMessage.error("系统错误!");
          }
        });
      } else {
        ElMessage.error("系统错误!");
      }
    },

    //  学期， 实验室， 周次， 时间明细， 
    // 新的 课程， 新的，班级 列表
    // 管理
    changePaikeButton() {
      console.log("改变"); // 校验参数

      if (this.KcOption && this.BjOption && this.BjOption.length != 0) {
        this.loading = true;
        Loading.show();
        this.$axios.post("/weixin-sysxk/changeXk", {
          "xnxq01id": this.Xnxq.xnxqh,
          "sysh": this.managePaike.sysh,
          "kkzc": this.managePaike.kkzc,
          "kksjmx": this.managePaike.kkDay + "-" + this.managePaike.kkSection,
          "jg0101id": this.userInfo.useraccount,
          "jgmc": this.userInfo.userrealname,
          // 新的
          "kcmc": this.KcOption,
          "bz": this.managePaike.bz,
          "classList": this.getBjOptionList // 班号 列表

        }).then(res => {
          const data = res.data;

          if (data.code == 200) {
            ElMessage.success("修改成功"); // 刷新课程表

            this.getXkListByXnxqAndJgId(this.Xnxq.xnxqh, this.userInfo.useraccount);
            this.manageVisible = false;
            this.loading = false;
            Loading.hide();
          } else {
            ElMessage.error(data.msg);
          }
        }).catch(error => {
          this.loading = false;
          Loading.hide();
        });
      } else {
        ElMessage.error("完成课程、班级选择");
      }
    }

  },
  computed: {
    getXkFilter() {
      return this.XkList.map(t => {
        t['address'] = t.yxsmc + t.sysmph + "教室";
        t['time'] = "第" + t.kkzc + "周,周" + t.kkDay + " 、" + this.sections[t.kkSection];
        t['bj'] = t.bjmcList.join('、');
        return t;
      }).filter(data => !this.search || data.kcmc.includes(this.search.toLowerCase()) || data.yxsmc.includes(this.search.toLowerCase()) || data.sysmph.includes(this.search.toLowerCase()) || data.jgmc.includes(this.search.toLowerCase()));
    },

    getKcList() {
      return this.KcAndBjList.map(item => {
        return item.kcmc;
      });
    },

    getBjOptionList() {
      return this.Bjlist.filter(i => {
        return this.BjOption.some(t => {
          return t == i.bh;
        });
      });
    }

  }
};